import { useState, useEffect } from 'react';

import { Button } from 'antd';
import './workSpace.scss';

const WorkSpace = ({item, setItem, onAdd, data, deleteItem}) => {

    const [name, setName] = useState('');
    const [descr, setDescr] = useState('');



    useEffect(() => {
        setName(item.name ? item.name : '')
        setDescr(item.descr ? item.descr : '')
        
    },[item])

    function onEdit(e) {
        e.preventDefault();

        if (name && name.length > 3) {
            onAdd(name, descr);
            setName('');
            setDescr('');
            setItem('')
            // deleteItem(item.id);
        }

        
    }

    function onNameChange(value) {
        setName(value)
    }

    function onDescrChange(value) {
        setDescr(value)
    }

    function onClose(e) {
        e.preventDefault();
        setName('');
        setDescr('')
        setItem('')
    }

    let className = 'wsTextArea wsTAName';

    if (name.length < 4) {
        className += ' red';
    }

    return (
        <div className='workSpace'>
            <form>
                <div className='wsTextAreas'>
                    <textarea 
                        className={className}
                        name="name"
                        placeholder='минимум 4 символа'
                        onChange={(e) => onNameChange(e.target.value)} 
                        value={name}>{name}</textarea>
                    <textarea 
                        className='wsTextArea wsTADescr' 
                        name="descr" cols="30" rows="7" 
                        onChange={(e) => onDescrChange(e.target.value)} 
                        value={descr}>{descr}</textarea>
                </div>
                <div className='wsBtns'>
                    <Button className='wsBtn' onClick={(e) => onClose(e)}>Закрыть</Button>
                    <Button className='wsBtn' onClick={(e) => onEdit(e)}>Сохранить</Button>
                </div>
            </form>
        </div> 
        
    )
}

export default WorkSpace;